@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;   
    font-family: 'Open Sans', sans-serif;
} 

body {
    display: flex;
    flex-direction: column;
    position: relative;
}
.app {
    height: 100vh;
    width: auto;
    display: grid;
    grid-template-columns: 20% 80%;
    grid-template-rows: 10% 90% ;
    grid-template-areas: 
    'sidebar navbar',
    'sidebar main';   
    position: relative;
}

.sidebar {
    display: flex;
    grid-area: sidebar;
    height: 200vh;
    width: 12rem;
    background-color: #fafafa;
    flex-direction: column;
}

.adduser-wrapper {
    display: grid;
    grid-area: main;
}
.usertable-wrapper {
    display: grid;
    grid-area: main;
}


*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.form-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
  height: 50%;
  border: 2px solid #4b3153;
  top: 20%;
  left: 35%;
  right: 50%;
}

.form-container #acct-no-input, .form-container #amount-input {
  width: 80%;
  height: 15%;
  border: none;
  border-bottom: 2px solid #4b3153;
  outline: none;
}

.form-container #submit-btn {
  width: 30%;
  padding: 0.4rem 0.3rem;
  border: none;
  background-color: #4b3153;
  color: #fafafa;
}
/*# sourceMappingURL=forms.css.map */
h1 {
  font-size: 1rem;
  margin: 1rem 2rem;
  color: #4b3153;
}

.usertable-container {
  border-collapse: collapse;
  margin: 0 2rem;
}

.thead-container {
  font-size: 0.8rem;
  padding: 2rem;
  border-bottom: 2px solid #4b3153;
  color: #4b3153;
}

tr:nth-of-type(even) {
  background-color: #d8d8d8;
}

td {
  font-size: 0.7rem;
  margin: 3rem;
  text-align: center;
  color: #4b3153;
  padding: 0.4rem 0;
  border-bottom: 1.5px solid #4b3153;
}

#add-acct-btn {
  padding: 0.4rem 0.2rem;
  font-size: 0.8rem;
  width: 6rem;
  -ms-grid-row-align: center;
  align-self: center;
  border-radius: 0;
  border: none;
  background-color: #4b3153;
  color: #fafafa;
  margin: 2rem;
  margin-left: 85%;
}

#search-input {
  padding: 0.4rem 0.2rem;
  font-size: 0.8rem;
  width: 12rem;
  margin: 2rem;
  border: 1px solid black;
  position: absolute;
  top: 9%;
}
/*# sourceMappingURL=usertable.css.map */
.modal-background {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  -webkit-transform: translate(-20%, -30%);
          transform: translate(-20%, -30%);
  z-index: 1;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}

#modal-wrapper {
  width: 15rem;
  height: 15rem;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 1rem;
}

#modal-wrapper #add-acct-form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-size: 0.8rem;
}

#modal-wrapper #add-acct-form #name-container, #modal-wrapper #add-acct-form #balance-container {
  display: flex;
  flex-direction: column;
}

#modal-wrapper #add-acct-form #x-btn {
  display: flex;
  justify-content: center;
  align-self: flex-end;
  width: 1.5rem;
  height: 1.5rem;
  padding: 0.5rem;
  border: none;
  margin-top: -15%;
  background-color: transparent;
  font-size: large;
  color: #4b3153;
}

#modal-wrapper #buttons {
  display: flex;
  justify-content: space-around;
}

#modal-wrapper #buttons #add-acct-submit-btn {
  padding: 0.4rem 0.3rem;
  border: none;
  background-color: #4b3153;
  color: #fafafa;
}

#modal-wrapper #buttons #cancel-btn {
  padding: 0.4rem 0.3rem;
  border: none;
}
/*# sourceMappingURL=addAcctModal.css.map */
.stats-container {
  display: flex;
  flex-direction: row;
  color: #4b3153;
}

.stats-container .stats-card::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 50px;
  bottom: 0;
  left: 0;
  z-index: -1;
  background-image: url(/banking-app/static/media/wavesOpacity.90206f6e.svg);
}

.stats-container .stats-card {
  position: relative;
  overflow: hidden;
  height: 115px;
  width: 25%;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.5);
  flex-wrap: wrap;
  font-size: 15px;
  margin-left: 33px;
}

.stats-container .stats-card .title {
  margin-left: 10px;
  margin-top: 8px;
  margin-right: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.stats-container .stats-card .stat {
  font-weight: 700;
  margin-left: 10px;
  font-size: 25px;
}

.welcome-container {
  height: 130px;
  width: 90%;
  background-color: #4b3153;
  margin-left: 33px;
  margin-top: 30px;
  display: flex;
  flex-direction: row;
}

.welcome-container h1, .welcome-container p {
  color: white;
}

.welcome-container h1 {
  margin: 20px 30px;
  font-size: 1.3125rem;
}

.welcome-container p {
  margin-left: 30px;
  font-size: 1.0625rem;
}

.welcome-container img {
  width: 150px;
  height: 84px;
  position: relative;
  left: 40px;
  top: 15px;
}
