h2 {
  padding: 1.2rem;
  font-size: 1.2rem;
  color: #4b3153;
}

.sidebar-list {
  width: 100%;
  height: auto;
  color: #4b3153;
  display: flex;
  flex-direction: column;
  padding: 0;
}

.sidebar-list .sidebar-list-row {
  width: 100%;
  height: 50px;
  font-size: 1rem;
  list-style-type: none;
  display: flex;
  padding: 1rem;
  cursor: pointer;
}

#icon {
  flex: 20% 1;
}

#title {
  flex: 80% 1;
  font-weight: bold;
}

#load-data-btn {
  padding: 0.4rem 0.2rem;
  font-size: 0.9rem;
  width: 78%;
  -ms-grid-row-align: center;
  align-self: center;
  border-radius: 0;
  border: none;
  background-color: #4b3153;
  color: #fafafa;
  margin-top: 10%;
}
